    animation-iteration-count: infinite;
}

@keyframes bubble{
    0%{
        transform: scale(0.5);
    }20%{
        transform: scale(0.7);
    }40%{
        transform: scale(1);
    }60%{
        transform: scale(0.7);
    }80%{
        transform: scale(0.5);
    }100%{
        transform: scale(0.3);
    }

}